package org.example.grademateweb.components

import androidx.compose.runtime.*
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.css.TextAlign
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.framework.annotations.DelicateApi
import com.varabyte.kobweb.silk.components.forms.Button
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.style.toModifier
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import kotlinx.coroutines.delay
import org.jetbrains.compose.web.attributes.placeholder
import org.jetbrains.compose.web.attributes.rows
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.*

@OptIn(DelicateApi::class)
@Composable
fun ContactUS(){
    var name by remember { mutableStateOf("") }
    var email by remember { mutableStateOf("") }
    var message by remember { mutableStateOf("") }

    var size by remember { mutableIntStateOf(50) }
    val bp = rememberBreakpoint()

    size = when {
        bp >= Breakpoint.LG -> 50
        bp >= Breakpoint.MD -> 70
        else -> 95
    }

    Column(
        modifier = Modifier
            .id("Contact Us")
            .fillMaxWidth()
            .background(GradeMateColors.Primary)
            .padding(24.px)
            .gap(24.px)
    ) {
        Form(
            attrs = Modifier
                .fillMaxWidth()
                    .color(GradeMateColors.Surface)
                .textAlign(TextAlign.Center)
                .toAttrs()
        ) {
            Fieldset {
                Legend(
                ){
                    H3 {
                        Text("Contact Us")
                    }
                }
                H5 {
                    Text("Get in touch with us")
                }
                //Name Input
                ContactInput(
                    placeholder = "Your Name",
                    text = name,
                    size = size,
                    onChange =  {
                        if(it.length <= 50){
                            name = it
                        }

                    }
                )
                Br {  }
                //Email Input
                ContactInputEmail(
                    placeholder = "Your Email",
                    text = email,
                    size = size,
                    onChange =  {
                        if(it.length <= 50){
                            email = it
                        }
                    }
                )
                Br {  }
                //Text Area
                TextArea(
                    attrs = Modifier
                        .fillMaxWidth(size.percent)
                        .padding(topBottom = 8.px, leftRight = 16.px)
                        .margin(bottom = 8.px)
                        .fontWeight(FontWeight.SemiBold)
                        .background(GradeMateColors.Primary)
                        .color(GradeMateColors.Surface)
                        .toAttrs{
                            rows(5)
                            placeholder("Your Message...")
                            onInput {
                                message = it.value
                            }
                        }
                    ,
                    value = message
                )
                Br {  }


                var successMessage by remember { mutableStateOf(false) }

                Button(
                    modifier = Modifier.fillMaxWidth(size.percent).then(androidButtonStyle.toModifier()),
                    onClick = {
                        val subject = "Contact Us Message from $name"
                        val body = """
            Name: $name
            Email: $email
            Message: $message
        """.trimIndent()
                        val mailto = "mailto:gradematedesktopapp@gmail.com?subject=${js("encodeURIComponent(subject)")}&body=${js("encodeURIComponent(body)")}"

                        // Open email client
                        js("window.location.href = mailto")

                        // Reset form fields
                        name = ""
                        email = ""
                        message = ""

                        // Show success message
                        successMessage = true
                    }
                ) {
                    Text("Send Message")
                }
                Br {  }

// Show success message if needed
                if (successMessage) {
                    Text(
                        "Message prepared! Check your email client to send it.",
                    )

                    // Hide the message after 3 seconds
                    LaunchedEffect(successMessage) {
                        delay(3000)
                        successMessage = false
                    }
                }





            }
        }
    }
}
