val buttonStyle = CssStyle {
    base {
        Modifier
            .border(style = LineStyle.None)
            .color(GradeMateColors.Surface)
            .padding(leftRight = 16.px, topBottom = 8.px)
            .margin(0.px)
            .borderRadius(8.px)
            .height(40.px)
            .fontSize(1.em)
            .textDecorationLine(TextDecorationLine.None)
            .fontWeight(FontWeight.Bold)

    }
    hover{
        Modifier.background(GradeMateColors.Back1)
            .cursor(Cursor.Pointer)
    }
    active{
        Modifier.background(GradeMateColors.Primary)
    }
}

