import org.jetbrains.compose.web.dom.Li
import org.jetbrains.compose.web.dom.Text
import org.jetbrains.compose.web.dom.Ul

@OptIn(DelicateApi::class)
@Composable
fun Overview(modifier: Modifier = Modifier) {

    Column(
        modifier = modifier
            .background(GradeMateColors.Back1)
            .color(GradeMateColors.Primary)
            .fontWeight(FontWeight.SemiBold)
            .padding(24.px)
            .gap(8.px)
    ) {

        // Title
        H3 {
            Text("Welcome to GradeMate")
        }

        // Intro paragraph (justified)
        Div(
            attrs = Modifier
                .textAlign(TextAlign.Justify)
                .toAttrs()
        ) {
            Text(
                "GradeMate is a comprehensive academic management platform designed to streamline grading, coursework, and student performance tracking. It provides powerful tools for tutors to manage modules, record grades, and monitor progress, while giving students a clear view of their results and coursework submissions. Before installing, ensure your system meets the following requirements:"
            )
        }

        // System Requirements List
        H3 {
            Text("System Requirements")
        }

        Ul {
            Li {
                Text("Windows: Windows 10 or newer, 4GB RAM, 500MB free disk space")
            }
            Li {
                Text("Linux: Ubuntu 20.04+, Debian 11+, 4GB RAM, 500MB free disk space")
            }
            Li {
                Text("Android: Android 8.0 (Oreo) or newer, 100MB free storage")
            }
        }

        // Optional note (also justified)
        Div(
            attrs = Modifier
                .margin(top = 16.px)
                .textAlign(TextAlign.Justify)
                .toAttrs()
        ) {

            Div {
                // Justified note
                Div(attrs = Modifier.textAlign(TextAlign.Justify).toAttrs()) {
                    Text(
                        "Once your system meets the requirements, download GradeMate for your platform and follow the installation guide."
                    )
                }

                // Download link styled as a button
                Anchor(
                    href = "#Downloads",
                    attrs = Modifier
                        .display(DisplayStyle.InlineBlock)
                        .color(GradeMateColors.Surface)
                        .padding(topBottom = 8.px, leftRight = 16.px)
                        .borderRadius(8.px)
                        .margin(top = 12.px)
                        .textDecorationLine(TextDecorationLine.None)
                        .then(windowsButtonStyle.toModifier())
                        .toAttrs()
                ) {
                    Text("Download Now")
                }
            }

        }
    }
}



