@OptIn(DelicateApi::class)
@Composable
fun Downloads(){
    val breakpoint = rememberBreakpoint()
    val isTablet = breakpoint == Breakpoint.MD
    val isDesktop = breakpoint >= Breakpoint.LG
    val cardWidth = when {
        isDesktop -> 30.percent  // 3 per row
        isTablet -> 45.percent   // 2 per row
        else -> 90.percent       // small phones: almost full width
    }
    Row (
        modifier = Modifier.fillMaxWidth()
            .display(DisplayStyle.Flex)
            .flexWrap(FlexWrap.Wrap)
            .gap(8.px)
            .color(GradeMateColors.Surface)
            .padding(8.px),
        horizontalArrangement = Arrangement.Center
    ){

        downloadItems.forEachIndexed { i, download ->

            val myStyle = when (i % 3) {
                0 -> windowsButtonStyle.toModifier()
                1 ->  linuxButtonStyle.toModifier()
                else -> androidButtonStyle.toModifier()
            }

            Button(
                attrs = Modifier
                    .width(cardWidth)
                    .height(100.px)
                    .then(myStyle)
                    .display(DisplayStyle.Flex)
                    .alignItems(AlignItems.Center)
                    .gap(12.px)
                    .padding(12.px)
                    .onClick {
                        window.open(download.link, "_blank")
                    }
                    .toAttrs()
            ) {
                // Icon
                Box(
                    modifier = Modifier.size(48.px),
                    contentAlignment = Alignment.Center
                ) {
                    download.icon()
                }

                // Text block
                Column(
                    verticalArrangement = Arrangement.Center,
                    horizontalAlignment = Alignment.Start
                ) {
                    H4(
                        attrs = Modifier
                            .color(GradeMateColors.Surface)
                            .fontWeight(FontWeight.ExtraBold)
                            .margin(bottom = 2.px)
                            .toAttrs()
                    ) {
                        Text(download.name)
                    }

                    P(
                        attrs = Modifier
                            .color(GradeMateColors.Surface)
                            .textAlign(TextAlign.Justify)
                            .margin(0.px)
                            .toAttrs()
                    ) {
                        Text(download.purpose)
                    }

                    P(
                        attrs = Modifier
                            .fontSize(12.px)
                            .color(GradeMateColors.Surface)
                            .margin(top = 4.px)
                            .toAttrs()
                    ) {
                        Text("Updated: ${download.released}")
                    }
                }
            }
        }


    }
}