@OptIn(DelicateApi::class)
@Composable
fun Documentation() {
    var selectedIndex by remember { mutableStateOf(0) }
    val bp = rememberBreakpoint()

    Column(
        modifier = Modifier
            .id("Documentation")
            .fillMaxWidth()
            .background(GradeMateColors.Surface)
            .padding(24.px)
            .gap(24.px)
    ) {

        H3(
            attrs = Modifier.fillMaxWidth()
                .color(GradeMateColors.Primary)
                .textAlign(TextAlign.Center)
                .toAttrs()
        ) {
            Text("User Documentation")
        }

        if (bp < Breakpoint.MD) {

            //  Small screens  stacked
            Column(
                modifier = Modifier.fillMaxWidth(),
                verticalArrangement = Arrangement.spacedBy(24.px)
            ) {
                Sidebar(
                    selectedIndex = selectedIndex,
                    onSelect = { selectedIndex = it },
                    modifier = Modifier.fillMaxWidth().borderRadius(12.px)
                        .maxHeight(500.px) // or any desired height
                        .overflow(Overflow.Scroll)
                        .padding(16.px)
                )

                MainContent(
                    selectedIndex = selectedIndex,
                    modifier = Modifier.fillMaxWidth()
                        .borderRadius(12.px)
                        .maxHeight(500.px) // or any desired height
                        .overflow(Overflow.Scroll)
                        .padding(16.px)

                )


            }

        } else {

            //  MD and above  side by side
            Row(
                modifier = Modifier.fillMaxWidth(),
                horizontalArrangement = Arrangement.spacedBy(24.px)
            ) {
                Sidebar(
                    selectedIndex = selectedIndex,
                    onSelect = { selectedIndex = it },
                    modifier = Modifier.fillMaxWidth(25.percent).borderRadius(12.px)
                        .maxHeight(500.px) // or any desired height
                        .overflow(Overflow.Scroll)
                        .padding(16.px)
                )

                MainContent(
                    selectedIndex = selectedIndex,
                    modifier = Modifier
                        .fillMaxWidth(75.percent)
                        .borderRadius(12.px)
                        .maxHeight(500.px) // or any desired height
                        .overflow(Overflow.Scroll)
                        .padding(16.px)
                )



            }
        }
    }
}
