import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Colors
import com.varabyte.kobweb.compose.ui.modifiers.attr
import com.varabyte.kobweb.compose.ui.modifiers.color
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxWidth
import com.varabyte.kobweb.compose.ui.modifiers.fontWeight
import com.varabyte.kobweb.compose.ui.modifiers.height
import com.varabyte.kobweb.compose.ui.modifiers.margin
import com.varabyte.kobweb.compose.ui.modifiers.padding
import com.varabyte.kobweb.silk.components.forms.Input
import org.jetbrains.compose.web.attributes.InputType
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.EmailInput
import org.jetbrains.compose.web.dom.TextInput

@Composable
fun ContactInput(
    placeholder: String,
    text: String,
    onChange: (String) -> Unit,
    required: Boolean = true,
    size: Int
) {
    com.varabyte.kobweb.silk.components.forms.TextInput(
        placeholder = placeholder,
        text = text,
        onTextChange = onChange,
        required = required,
        modifier = Modifier.height(50.px)
            .fillMaxWidth(size.percent)
            .padding(topBottom = 8.px, leftRight = 16.px)
            .margin(bottom = 8.px)
            .color(GradeMateColors.Surface)
            .fontWeight(FontWeight.SemiBold)
    )
}
@Composable
fun ContactInputEmail(
    placeholder: String,
    text: String,
    onChange: (String) -> Unit,
    required: Boolean = true,
    size: Int
) {
    val emailRegex = Regex("""^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,}$""")
    val isValid = text.isEmpty() || emailRegex.matches(text)

    Input(
        type = InputType.Email,
        placeholder = placeholder,
        value = text,
        onValueChange = onChange,
        required = required,
        modifier = Modifier
            .height(50.px)
            .fillMaxWidth(size.percent)
            .padding(topBottom = 8.px, leftRight = 16.px)
            .margin(bottom = 8.px)
            .fontWeight(FontWeight.SemiBold)
            .attr(
                "pattern",
        "^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\\.[A-Za-z]{2,}$"
    ).attr(
        "title",
        "Enter a valid email address (e.g. user@example.com)"
    ).color(
                if (isValid) GradeMateColors.Surface
                else Colors.Red
            ),

    )
}




@Composable
fun ContactInputTel(
    placeholder: String,
    text: String,
    onChange: (String) -> Unit,
    required: Boolean = false, // optional field
    size: Int
) {
    // Regex explanation:
    // ^\+         -> must start with +
    // [0-9]{9,12} -> allow 9 to 12 digits after the +
    val phoneRegex = Regex("""^\+[0-9]{9,12}$""")

    // Valid if empty (optional) or matches regex
    val isValid = text.isEmpty() || phoneRegex.matches(text)

    Input(
        type = InputType.Tel,
        placeholder = placeholder,
        value = text,
        onValueChange = {
            // Allow typing only if empty or starts with + and length <= 14
            if (it.isEmpty() || (it.startsWith("+") && it.length <= 14)) {
                onChange(it)
            }
        },
        required = required,
        modifier = Modifier
            .height(50.px)
            .fillMaxWidth(size.percent)
            .padding(topBottom = 8.px, leftRight = 16.px)
            .margin(bottom = 8.px)
            .fontWeight(FontWeight.SemiBold)
            .attr("pattern", """\+[0-9]{9,13}""") // allow 9-13 digits after +
            .attr("title", "Phone number must start with + and contain 9-13 digits")
            .color(if (text.isEmpty() || Regex("""^\+[0-9]{9,13}$""").matches(text)) GradeMateColors.Surface else Colors.Red)
    )

}


