import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.background
import com.varabyte.kobweb.compose.ui.modifiers.color
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxWidth
import com.varabyte.kobweb.compose.ui.modifiers.fontWeight
import com.varabyte.kobweb.compose.ui.modifiers.gap
import com.varabyte.kobweb.compose.ui.modifiers.height
import com.varabyte.kobweb.compose.ui.modifiers.padding
import com.varabyte.kobweb.framework.annotations.DelicateApi
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import org.example.grademateweb.components.CircularDot
import org.example.grademateweb.components.GradeMateColors
import org.example.grademateweb.components.LoginInstructions
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.H3
import org.jetbrains.compose.web.dom.Text

@OptIn(DelicateApi::class)
@Composable
fun AndroidGuide(modifier: Modifier) {

    val breakpoint = rememberBreakpoint()
    val isMobile = breakpoint <= Breakpoint.MD

    Column(
        modifier = modifier
            .background(GradeMateColors.Back1)
            .color(GradeMateColors.Primary)
            .fontWeight(FontWeight.SemiBold)
            .padding(24.px)
            .gap(8.px)
    ) {

        H3 {
            Text("Android App Guide")
        }

        Row(
            modifier = Modifier.gap(8.px),
            verticalAlignment = Alignment.CenterVertically
        ) {
            CircularDot("1")
            Text("Sign In")
        }

        //  Responsive Section
        if (isMobile) {
            // SMALL SCREENS  Stack vertically
            Column(
                modifier = Modifier
                    .fillMaxWidth()
                    .gap(16.px)
            ) {

                Image(
                    src = "signandroid.jpeg",
                    modifier = Modifier
                        .fillMaxWidth()
                        .height(250.px)
                )

                LoginInstructions()
            }
        } else {
            // LARGE SCREENS  Side by side
            Row(
                modifier = Modifier
                    .fillMaxWidth()
                    .gap(24.px),
                verticalAlignment = Alignment.Top
            ) {

                Image(
                    src = "signandroid.jpeg",
                    modifier = Modifier
                        .fillMaxWidth(50.percent)
                        .height(300.px)
                )

                LoginInstructions()
            }
        }
    }
}
