@OptIn(DelicateApi::class)
@Composable
fun WelcomeSection() {
    val breakpoint = rememberBreakpoint()

    val isMobile = breakpoint <= Breakpoint.SM
    val isTablet = breakpoint == Breakpoint.MD
    val isDesktop = breakpoint >= Breakpoint.LG

    // Modern background gradient using backgroundImage
    val backgroundModifier = Modifier
        .fillMaxWidth()
        .padding(16.px)
        .gap(24.px)
        .backgroundImage(
            linearGradient(GradeMateColors.Back1,
                GradeMateColors.Primary
            )
        ).color(GradeMateColors.Surface)
        .padding(24.px)
   //Welcome Section
    Column(
        modifier = Modifier.fillMaxWidth().id("Home")
    ) {
        if (isMobile) {
            Column(
                modifier = backgroundModifier,
                horizontalAlignment = Alignment.CenterHorizontally,
                verticalArrangement = Arrangement.spacedBy(16.px)
            ) {
                H3(
                    attrs = Modifier.fontSize(24.px)
                        .fontWeight(FontWeight.Bold)
                        .padding(0.px)
                        .margin(0.px)
                        .color(GradeMateColors.Primary)
                        .toAttrs()
                ) { Text("Welcome to GradeMate") }

                P(
                    attrs = Modifier.fontSize(16.px)
                        .margin(0.px)
                        .toAttrs()
                ) {
                    Text("Student Grading & Records Platform")
                    Br()
                    Text("for Multiple Institutions operating ")
                    Br()
                    Text("under NACTVET Regulations")
                }

                Image(
                    src = "welcome.jpeg",
                    modifier = Modifier
                        .fillMaxWidth()
                        .height(180.px)
                        .borderRadius(16.px)
                )
                Button(
                    attrs = Modifier.then(windowsButtonStyle.toModifier())
                        .onClick {
                            window.location.hash = "#Downloads"
                        }
                        .toAttrs()
                ){
                    Text("Download Now")
                }

            }
        } else {
            Row(
                modifier = backgroundModifier.id("Home"),
                verticalAlignment = Alignment.CenterVertically,
                horizontalArrangement = Arrangement.spacedBy(24.px)
            ) {
                // Text Column
                Column(
                    modifier = Modifier.flexBasis(
                        when {
                            isTablet -> 50.percent // Tablet: 50%
                            isDesktop -> 30.percent // Desktop: 20%
                            else -> 50.percent
                        }
                    ).gap(12.px),
                    verticalArrangement = Arrangement.spacedBy(12.px)
                ) {
                    H3(
                        attrs = Modifier.fontSize(28.px)
                            .fontWeight(FontWeight.Bold)
                            .padding(0.px)
                            .margin(0.px)
                            .color(GradeMateColors.Primary)
                            .toAttrs()
                    ) { Text("Welcome to GradeMate") }

                    P(
                        attrs = Modifier.fontSize(16.px)
                            .fontWeight(FontWeight.Bold)
                            .margin(0.px)
                            .toAttrs()
                    ) {
                        Text("Student Grading & Records Platform")
                        Br()
                        Text("for Multiple Institutions operating ")
                        Br()
                        Text("under NACTVET Regulations")
                    }
                    Button(
                        attrs = Modifier.then(windowsButtonStyle.toModifier())
                            .onClick {
                                window.location.hash = "#Downloads"
                            }
                            .toAttrs()
                    ){
                        Text("Download Now")
                    }
                }

                // Image Column
                Image(
                    src = "welcome.jpeg",
                    modifier = Modifier.flexBasis(
                        when {
                            isTablet -> 50.percent // Tablet: 50%
                            isDesktop -> 70.percent // Desktop: 80%
                            else -> 50.percent
                        }
                    )
                        .height(if (isTablet) 250.px else 350.px)
                        .borderRadius(16.px)

                )
            }
        }
        Row (
            modifier = Modifier.fillMaxWidth()
                .display(DisplayStyle.Flex)
                .flexWrap(FlexWrap.Wrap)
                .gap(8.px)
                .padding(8.px),
            horizontalArrangement = Arrangement.Center
        ){

            //Manage Students
            WelcomeCard(
                icon = {
                    Image(src = "student.png",
                        modifier = Modifier.size(60.px).color(GradeMateColors.Primary))
                },
                title = "Manage Students & Tutors",
                fullText = "Easily add , update & easily track student's and tutor's records.",
            )
            //Handle Programmes && Modules
            WelcomeCard(
                icon = {
                    Image(src = "folder.png",
                        modifier = Modifier.size(60.px).color(GradeMateColors.Primary))
                },
                title = "Handle Programmes & Modules",
                fullText = "Organize programmes and manage modules across departments.",

            )
            //Manage Students
            WelcomeCard(
                icon = {
                    Image(src = "chart.png",
                        modifier = Modifier.size(60.px).color(GradeMateColors.Primary))
                },
                title = "Grades & Reports",
                fullText = "Generate graded results,reports,transcripts and best students certificates",
            )
        }
    }

}



