package org.example.grademateweb.components

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.framework.annotations.DelicateApi
import com.varabyte.kobweb.silk.components.icons.fa.FaEnvelope
import com.varabyte.kobweb.silk.components.icons.fa.FaTiktok
import com.varabyte.kobweb.silk.components.icons.fa.FaWhatsapp
import com.varabyte.kobweb.silk.components.icons.fa.IconSize
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.style.toModifier
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import kotlinx.browser.window
import org.jetbrains.compose.web.css.DisplayStyle
import org.jetbrains.compose.web.css.FlexDirection
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.*

@OptIn(DelicateApi::class)
@Composable
fun AppNavFooter() {
    val breakpoint = rememberBreakpoint()
    val isMobile = breakpoint <= Breakpoint.SM

    Column(
        modifier = Modifier
            .fillMaxWidth()
            .background(GradeMateColors.Primary)
            .padding(24.px)
            .gap(24.px)
            .id("#Downloads")
    ) {
        // Main Columns
        Row(
            modifier = Modifier.fillMaxWidth()
                .gap(32.px)
                .display(if (isMobile) DisplayStyle.Block else DisplayStyle.Flex),
            verticalAlignment = Alignment.Top
        ) {
            // Branding Column
            Column(
                modifier = if (isMobile) Modifier.fillMaxWidth() else Modifier.flexBasis(33.percent),
                verticalArrangement = Arrangement.spacedBy(12.px)
            ) {
                H5(
                    attrs = Modifier
                        .fontSize(24.px)
                        .color(GradeMateColors.Surface)
                        .margin(0.px)
                        .padding(0.px)
                        .toAttrs()
                ) { Text("GradeMate") }

                P(
                    attrs = Modifier
                        .color(GradeMateColors.Surface)
                        .fontSize(14.px)
                        .fontWeight(FontWeight.Bold)
                        .toAttrs()
                ) {
                    Text("Student Grading & Records Platform")
                    Br()
                    Text("for Multiple Institutions operating ")
                    Br()
                    Text("under NACTVET Regulations")

                }

                Row(
                    modifier = Modifier.gap(16.px),
                    verticalAlignment = Alignment.CenterVertically
                ) {
                    FaWhatsapp(size = IconSize.XL, modifier = Modifier
                        .onClick {
                            window.open("https://wa.me/255686777093", "_blank")
                        }
                        .then(linkStyle.toModifier())
                        .color(GradeMateColors.Surface).padding(24.px).borderRadius(50.percent))
                    FaTiktok( size = IconSize.XL,  modifier = Modifier.onClick {
                        window.open("https://www.tiktok.com/@lennycodes2000","_blank")
                    }
                        .then( linkStyle
                            .toModifier())
                        .color(GradeMateColors.Surface)
                        .padding(24.px).borderRadius(50.percent))
                    FaEnvelope(
                        size = IconSize.XL,
                        modifier = Modifier
                            .onClick {
                            window.open("mailto:gradematedesktopapp@gmail.com","_blank")
                        }
                            .then( linkStyle
                            .toModifier())
                            .color(GradeMateColors.Surface)
                            .padding(24.px).borderRadius(50.percent)
                    )

                    // Add more icons if needed
                }
            }

            // Product Column
            Column(
                modifier = if (isMobile) Modifier.fillMaxWidth() else Modifier.flexBasis(33.percent),
                verticalArrangement = Arrangement.spacedBy(8.px)
            ) {
                H5(
                    attrs = Modifier
                        .fontSize(20.px)
                        .color(GradeMateColors.Surface)
                        .padding(leftRight = 16.px, topBottom = 8.px)
                        .margin(0.px)
                        .toAttrs()
                ) { Text("Product") }

                Ul(
                    attrs = Modifier
                        .gap(4.px)
                        .display(DisplayStyle.Flex)
                        .flexDirection(FlexDirection.Column)
                        .padding(0.px)
                        .margin(0.px)
                        .toAttrs()
                ) {
                    productList.forEach { item ->
                        NavLink(
                            link = item.link, // avoid browser navigation
                            label = item.name,
                            icon = item.icon
                        )
                    }

                }
            }

            // Contact Column
            Column(
                modifier = if (isMobile) Modifier.fillMaxWidth() else Modifier.flexBasis(33.percent),
                verticalArrangement = Arrangement.spacedBy(8.px)
            ) {
                H5(
                    attrs = Modifier
                        .fontSize(20.px)
                        .color(GradeMateColors.Surface)
                        .padding(leftRight = 16.px, topBottom = 8.px)
                        .margin(0.px)
                        .toAttrs()
                ) { Text("Contact") }

                Ul(
                    attrs = Modifier
                        .gap(4.px)
                        .display(DisplayStyle.Flex)
                        .flexDirection(FlexDirection.Column)
                        .padding(0.px)
                        .margin(0.px)
                        .toAttrs()
                ) {
                    contactList.forEach { item ->
                        NavLink(link = "#${item.name}", label = item.name, icon = item.icon)

                    }
                }
            }
        }

        // Footer Bottom Text
        Row(
            modifier = Modifier.fillMaxWidth()
                .color(GradeMateColors.Surface)
                .fontSize(14.px),
            horizontalArrangement = Arrangement.Center
        ) {
            Text(
                " 2025 GradeMate. All rights reserved.")
        }
    }
}

