import org.jetbrains.compose.web.css.LineStyle
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.H4
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Text

@OptIn(DelicateApi::class)
@Composable
fun WelcomeCard(
    icon: @Composable () -> Unit,
    title: String,
    fullText: String,
) {
    val breakpoint = rememberBreakpoint()
    val isMobile = breakpoint <= Breakpoint.SM
    val isTablet = breakpoint == Breakpoint.MD
    val isDesktop = breakpoint >= Breakpoint.LG
    val cardWidth = when {
        isDesktop -> 30.percent  // 3 per row
        isTablet -> 45.percent   // 2 per row
        else -> 90.percent       // small phones: almost full width
    }


    Row(
        modifier = Modifier
            .id("Downloads")
            .width(cardWidth)
            .height(120.px)
            .padding(leftRight = 12.px, topBottom = 8.px)
            .borderRadius(12.px)
            .gap(16.px)
            .border(width = 1.px, color = GradeMateColors.Back1, style = LineStyle.Solid)
            .boxShadow(0.px, 2.px, 4.px, color =  com.varabyte.kobweb.compose.ui.graphics.Color.rgba(0,0,0,0.1.toInt()))   // subtle shadow
            .boxShadow(0.px, 4.px, 8.px, color = GradeMateColors.Back1),  // deeper shadow for depth            .background(GradeMateColors.Surface),
        verticalAlignment = Alignment.CenterVertically
    ) {
        // Icon
        Box(
            modifier = Modifier.margin(leftRight = 8.px)
                .size(35.px),
            contentAlignment = Alignment.Center
        ) {
            icon()
        }

        // Text
        Column(
            verticalArrangement = Arrangement.Center,
            horizontalAlignment =  Alignment.Start
        ) {
            H4(
                attrs = Modifier
                    .color(GradeMateColors.Primary)
                    .padding(0.px)
                    .margin(bottom = 4.px)
                    .toAttrs()
            ) {
                Text(title)
            }
            P(
                attrs = Modifier
                    .margin(0.px)
                    .padding(0.px)
                    .textAlign(TextAlign.Justify)
                    .color(GradeMateColors.Primary)
                    .toAttrs()
            ) {
                Text(fullText)
            }
        }
    }
}
