package org.example.grademateweb.components

import androidx.compose.runtime.*
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.css.ListStyleType
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Color
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.framework.annotations.DelicateApi
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.style.toModifier
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import kotlinx.browser.window
import org.jetbrains.compose.web.css.*
import org.jetbrains.compose.web.dom.*

@OptIn(DelicateApi::class)
@Composable
fun NavBar() {
    val links = listOf("Home", "Documentation", "Downloads")

    val breakpoint = rememberBreakpoint()
    val isMobile = breakpoint <= Breakpoint.MD
    var menuOpen by remember { mutableStateOf(false) }

    /* =======================
       TOP NAVBAR
       ======================= */
    Row(
        modifier = Modifier
            .fillMaxWidth()
            .height(64.px)
            .background(GradeMateColors.Primary)
            .padding(leftRight = 16.px, topBottom = 8.px)
            .color(GradeMateColors.Surface)
            .fontWeight(FontWeight.Bold)
            .gap(20.px)
            .position(Position.Sticky)
            .top(0.px)
            .zIndex(1000)
            .boxShadow(0.px, 2.px, 8.px, color = rgba(0, 0, 0, 0.15)),
        verticalAlignment = Alignment.CenterVertically
    ) {
        // Logo
        Image(
            src = "grademate.png",
            alt = "GradeMate",
            modifier = Modifier
                .size(50.px)
                .borderRadius(50.percent)
        )

        H3 { Text("GradeMate") }

        // Spacer
        Span(attrs = Modifier.weight(1).toAttrs())

        if (!isMobile) {
            /* ===== Desktop Menu ===== */
            Ul(
                attrs = Modifier
                    .display(DisplayStyle.Flex)
                    .listStyle(ListStyleType.None)
                    .gap(30.px)
                    .toAttrs()
            ) {
                navList.forEach { link ->
                    Li {
                        NavLink(
                            link = "#${link.name}",
                            label = link.name,
                            icon = link.icon,
                            onClick = { menuOpen = false }
                        )
                    }
                }

            }
            Button(
                attrs = Modifier
                    .onClick {
                        window.location.hash = "#Contact Us"
                    }
                    .then(androidButtonStyle.toModifier())
                    .toAttrs()
            ){
                Text("Contact Us")
            }
        } else {
            /* ===== Mobile Hamburger ===== */
            Button(
                attrs = Modifier
                    .background(org.jetbrains.compose.web.css.Color.transparent)
                    .border(style = LineStyle.None)
                    .color(GradeMateColors.Surface)
                    .fontSize(24.px)
                    .onClick { menuOpen = !menuOpen }
                    .toAttrs()
            ) {
                Text("")
            }
        }
    }

    /* =======================
       MOBILE DROPDOWN MENU
       ======================= */
    if (isMobile && menuOpen) {
        Ul(
            attrs = Modifier
                .fillMaxWidth()
                .background(GradeMateColors.Primary)
                .listStyle(ListStyleType.None)
                .padding(16.px)
                .display(DisplayStyle.Flex)
                .flexDirection(FlexDirection.Column)
                .gap(12.px)
                .zIndex(999)
                .toAttrs()
        ) {
            navList.forEach { link ->
                Li {
                    NavLink(
                        link = "#${link.name}",
                        label = link.name,
                        icon = link.icon,
                        onClick = { menuOpen = false }
                    )
                }
            }


            Li {
                Button(
                    attrs = Modifier
                        .onClick {
                            window.location.hash = "#Contact Us"

                        }
                    .then(androidButtonStyle.toModifier())
                        .toAttrs()
                ){
                    Text("Contact Us")
                }
            }
        }
    }
}
