package org.example.grademateweb.components

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxWidth
import com.varabyte.kobweb.compose.ui.modifiers.gap
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.Li
import org.jetbrains.compose.web.dom.Text
import org.jetbrains.compose.web.dom.Ul

@Composable
fun LoginInstructions() {
    Column(
        modifier = Modifier
            .fillMaxWidth()
            .gap(8.px)
    ) {
        Ul {
            Li {
                Text("Username: Enter your registration number (e.g., NS1000/0010/2024).")
            }
            Li {
                Text("Password (first-time users): Use your last name in uppercase. If the last name has less than 6 characters, add '1' until it reaches 6 characters.")
            }
            Li {
                Text("Examples:")
                Ul {
                    Li { Text("JUMA  JUMA11") }
                    Li { Text("GONZALEZ  GONZALEZ") }
                    Li { Text("KIM  KIM111") }
                    Li { Text("MWAKI  MWAKI1") }
                }
            }
            Li {
                Text("After your first login, change your password for security.")
            }
        }
    }
}
