@Composable
fun NavLink(
    link: String,
    label: String,
    onClick: () -> Unit = {},
    icon: @Composable () -> Unit = {}
) {
    Anchor(
        href = link,
        attrs = linkStyle.toModifier()
            .display(DisplayStyle.Flex)
            .alignItems(AlignItems.Center)
            .gap(8.px)
            .textDecorationLine(TextDecorationLine.None)
            .onClick {
                window.location.hash = link
            }
            .toAttrs()
    ) {
        icon()
        Text(label)
    }
}



