package org.example.grademateweb.components

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Colors
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.framework.annotations.DelicateApi
import com.varabyte.kobweb.silk.components.icons.fa.*
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import org.jetbrains.compose.web.css.AlignItems
import org.jetbrains.compose.web.css.DisplayStyle
import org.jetbrains.compose.web.css.JustifyContent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.Li
import org.jetbrains.compose.web.dom.Text

data class DocItems (
    val name: String,
    val icon:@Composable ()-> Unit
)
val docItems = mutableListOf(
    DocItems(name = "Getting Started", icon = { FaChartColumn(size = IconSize.XL) }),
    DocItems(name = "Desktop App Guide", icon = { FaComputer(size = IconSize.XL) }),
    DocItems(name = "Android App Guide", icon = { FaGooglePlay(size = IconSize.XL) }),
    DocItems(name = "Data and Security", icon = { FaDatabase(size = IconSize.XL) }),
    DocItems(name = "FAQs", icon = { FaCircleQuestion(size = IconSize.XL) }),
)
@OptIn(DelicateApi::class)
@Composable
fun DocItem(
    value: String,
    icon: @Composable () -> Unit,
    isSelected: Boolean,
    onClick: () -> Unit,
    modifier: Modifier = Modifier
) {
    val breakpoint = rememberBreakpoint()

    Li(
        attrs = modifier
            .display(DisplayStyle.Flex)
            .title(value)
            .alignItems(AlignItems.Center)
            .justifyContent(JustifyContent.FlexStart)
            .gap(12.px)
            .padding(topBottom = 10.px, leftRight = 16.px)
            .color(
                if (isSelected) GradeMateColors.Surface
                else GradeMateColors.Primary
            )
            .background(
                color = if (isSelected) GradeMateColors.Primary else Colors.Transparent
            )
            .cursor(com.varabyte.kobweb.compose.css.Cursor.Pointer)
            .onClick { onClick() }
            .toAttrs()
    ) {

        org.jetbrains.compose.web.dom.Div(
            attrs = Modifier
                .width(36.px)
                .display(DisplayStyle.Flex)
                .justifyContent(JustifyContent.Center)
                .alignItems(AlignItems.Center)
                .toAttrs()
        ) {
            icon()
        }
            Text(value)
    }
}



